.nav-main ul {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: flex-end;
  width: 80vw;
  list-style-type: none;
  margin-top: 1rem;
}

.nav-main {
  background: #D4F1F4;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: auto;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}
.nav-main img {
  height: 6rem;
  padding: 0.5rem 0;
  transition: height 0.3s ease-in-out;
  -webkit-transition: height 0.3s ease-in-out;
}
.nav-main a {
  text-decoration: none;
  color: #075869;
  font-size: 1rem;
  font-weight: bold;
  padding: 0 1rem 0 1rem;
}
.nav-main a:hover {
  color: #189AB4;
}
.nav-main .nav-donate {
  color: #B56018;
}
.nav-main .nav-donate:hover {
  color: #189AB4;
}
.nav-main .mobile-nav {
  display: none;
}

@media (min-width: 320px) and (max-width: 800px) {
  .nav-main {
    width: 100%;
  }
  .nav-main ul {
    display: flex;
    position: absolute;
    top: -35rem;
    width: 100vw;
    height: auto;
    background-color: #D4F1F4;
    flex-direction: column;
    justify-content: space-between;
    opacity: 0.95;
    transition: 0.5s ease-out;
  }
  .nav-main ul li {
    font-size: 2rem;
    font-weight: bold;
    padding-bottom: 3rem;
  }
  .nav-main #mobile {
    top: 5rem;
    transition: 0.5s ease-in;
  }
  .nav-main .mobile-nav {
    display: block;
    margin-right: 2rem;
    height: 3rem;
    width: auto;
  }
}

