.contact-content iframe {
  grid-column: 2;
  grid-row: 1;
  margin-top: -8rem;
  width: 80%;
  height: 20rem;
  border: 0;
}

.form-text {
  margin-bottom: 1rem;
}

.contact-info {
  grid-column: 2;
  grid-row: 1;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 20rem;
}
.contact-info a {
  text-decoration: none;
}

.days {
  text-align: center;
}
.days h3 {
  color: #B56018;
}

.tel {
  background-color: #B56018;
  color: #fff;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
}

.facebook-logo {
  height: 2rem;
  width: auto;
}

@media (min-width: 320px) and (max-width: 480px) {
  .contact-head h5 {
    font-size: 1rem;
  }

  .contact-content {
    display: flex;
    flex-direction: column;
  }
  .contact-content iframe {
    margin: 4rem 0 0 0;
    width: 100%;
    height: 30rem;
  }

  .contact-info {
    margin-top: 1rem;
  }
}

/*# sourceMappingURL=contact.css.map */

.help-main .card {
  width: 20rem;
  background-color: #75E6DA;
  align-items: center;
}
.help-main .card .card-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
.help-main .card .card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.help-main {
  height: auto;
  margin: 5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
@media (min-width: 320px) and (max-width: 480px) {
  .help-main {
    flex-direction: column;
    align-items: center;
  }

  .card {
    margin-bottom: 3rem;
  }
}

/*# sourceMappingURL=help.css.map */

.food-stats-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(5, auto);
  margin-top: 5rem;
}
.food-stats-main img {
  height: auto;
  width: 100%;
}

article {
  margin: 1rem;
}

.graph-one {
  grid-row: 1;
  grid-column: 1;
}

.graph-one-stats {
  grid-row: 1;
  grid-column: 2;
}

.graph-two {
  grid-row: 2;
  grid-column: 2;
  margin-top: 3rem;
}

.graph-two-stats {
  grid-row: 2;
  grid-column: 1;
  margin-top: 3rem;
}

.graph-three {
  grid-row: 3;
  grid-column: 1;
  margin-top: 3rem;
}

.graph-three-stats {
  grid-row: 3;
  grid-column: 2;
  margin-top: 3rem;
}

.graph-four {
  grid-row: 4;
  grid-column: 2;
  margin-top: 3rem;
}

.graph-four-stats {
  grid-row: 4;
  grid-column: 1;
  margin-top: 3rem;
}

.other-links {
  grid-row: 5;
  grid-column: 1/span 2;
  text-align: center;
  margin-top: 3rem;
}
.other-links li {
  list-style: none;
  width: 18rem;
  margin: auto;
}

@media (min-width: 320px) and (max-width: 480px) {
  .stats-main p {
    display: none;
  }
  .stats-main .article-link {
    display: block;
    font-size: 1rem;
  }
}

/*# sourceMappingURL=stats.css.map */

